
header {
    position: relative;
    z-index: 2;
    height: 80px;
    width: 100%;
    /*box-shadow: 0 2px 2px #ccc;*/
    border-bottom: 1px solid #cccccc;
}
#header {
    height: 80px;
    line-height: 80px;
}
.logo {
    padding-top: 0px;
    padding-bottom: 0px;
    /*
    padding-top: 20px;
    padding-bottom: 20px;
    */
}
.userbar {
    float: right;
    position: relative;
}
.userbar:hover .triangle {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}
.user-dropdown {
    padding-right: 20px;
}
.userbar a {
    display: inline-block;
    line-height: 60px;
    margin-left: 10px;
    text-decoration: none;
    color: #91B0C4;
    font-size: 14px;
}
.triangle {
    position: absolute;
    right: 0;
    top: 38px;
    width: 0;
    height: 0;
    border-style: solid;
    border-left: 6px double transparent;
    border-right: 6px double transparent;
    border-bottom: 0;
    border-top: 6px solid #91B0C4;
    transition: -webkit-transform .3s;
    transition: transform .3s;
    transition: transform .3s, -webkit-transform .3s;
}
.userbar:hover .user-controller {
    display: block;
    right: -46px;
    top: 80px;
    opacity: 0.99;
}
.user-controller {
    position: absolute;
    display: none;
    right: -46px;
    top: 80px;
    width: 200px;
    background: rgba(255, 255, 255, 0.95);
    box-shadow: 3px 0px 5px #eef3f5, -3px 5px 5px #eef3f5;
    border-radius: 5px;
    opacity: 0;
    transition: all 1s;
}
.user-controller a {
    display: block;
    padding: 10px;
    margin: 0;
    line-height: 1.2;
    cursor: pointer;
}
.user-controller a:hover {
    background-color: #f3f7fa;
    text-decoration: none;
}
.changeEmail {
    margin-bottom: -3px;
}
.changeEmailInput {
    background: #f3f7fa;
    border-radius: 20px !important;
    padding-left: 25px !important;
    font-size: 12px;
    color: rgb(153, 153, 153);
    width: 270px !important;
    height: 30px !important;
}



