
#retrieveGray {
    background-color: gray !important;
}
.login-modal-header {
    text-align: center;
    font-size: 20px;
    font-weight: bolder;
    margin-bottom: 10px;
}
.login-modal-dialog .form-group {
    width: 100%;
}
.login-form, .reg-form {
    width: 400px;
    float: right;
    padding-top: 25px;
}
.reg-form {
    margin-top: 10px;
}
.login-form input, .reg-form input {
    padding: 0;
}
.remember-label {
    position: relative;
    display: block;
    width: 100%;
    height: 20px;
    top: -35px;
    font-size: 12px;
    margin: 0 auto;
    /* line-height: 14px; */
    text-align: center;
    font-size: 12px;
    margin-top: 44px;
    padding-left: 48px;
    margin-bottom: -7px;
}
.remember-name {
    position: relative;
    top: -13px;
    color: rgb(153, 153, 153);
}
.remember-img {
    padding: 10px;
    background-color: #fff;
}
.login-modal input {
    color: rgb(153, 153, 153) !important;
    background-color: #f8f8f8 !important;
}
input.login-username {
    margin-top: 10px;
    font-size: 12px;
}
input.login-password {
    margin-top: 10px;
    font-size: 12px;
}
.login-remember {
    margin: 1px 4px 0 0;
    vertical-align: top;
    display: block;
    margin: 0 auto 5px;
}
.login-btn, .reg-btn, .login-modal-reg {
    height: 50px;
    margin-top: 10px;
    line-height: 30px;
    border: none;
    outline: none;
    color: #fff;
    cursor: pointer;
}
.btn.login-btn {
    /*background-image: url('../../../static/img/pri-bg.png');*/
    margin-top: -5px;
    margin-bottom: 0px;
    font-size: 12px;
    font-family: "Microsoft yahei";
    width: 238px !important;
    height: 34px !important;
    border-radius: 38px;
    background-color: #02a1f5 !important;
}
.btn.reg-btn, .login-modal-reg {
    /*background-image: url('../../../static/img/register.png');*/
    /*width: 173px !important;*/
    /*height: 50px !important;*/
    margin-top: 0px;
    margin-bottom: 10px;
    font-size: 12px;
    font-family: "Microsoft yahei";
    width: 238px !important;
    height: 34px !important;
    border-radius: 38px;
    background-color: #ff5722 !important;
}
.btn.login-btn, .btn.reg-btn, .login-modal-reg {
    background-color: #fff;
    box-shadow: none;
    width: 40%;
    margin-left: 36%;
    background-size: 100% 100%;
}
.login-btn + .login-btn {
    margin-top: 10px;
}
.login-btn:hover, .reg-btn:hover, .login-modal-reg:hover {
    box-shadow: none;
}
hr {
    width: 80%;
    border: none;
    border-bottom: 1px solid #999;
}
.forget-form {
    width: 400px;
    float: right;
    margin-top: 100px;
}
.forget-btn, .logup-btn {
    font-size: 12px;
    cursor: pointer;
    vertical-align: sub;
    margin-left: 10px;
    color: rgb(153, 153, 153);
    text-decoration: underline;
}
.input-group .input-icon > i {
    color: #ccc;
    display: block;
    position: absolute;
    margin: 14px 2px 0px 12px;
    z-index: 3;
    width: 16px;
    font-size: 16px;
    text-align: center;
}
.input-group .input-icon .fa-user {
    margin-top: 26px;
}
.input-group .input-icon input {
    padding-left: 35px;
    border-radius: 31px;
    /*border: 1px solid #999;*/
    margin-top: 10px;
}
.nopassword {
    margin-left: 198px;
}
.input-group {
    padding-left: 121px;
}
.login-form .form-group {
    margin-bottom: 20px;
}
.login-modal {
    overflow: auto;
    width: 100% !important;
    min-height:666px !important;
    background-image: url('../../../static/img/bg.jpg');
    background-size: cover;
    background-color: rgba(200, 200, 200, 1) !important;
}
.login-modal-dialog {
    width: 657px !important;
    min-height: 342px !important;
    background-image: url('../../../static/img/1280.png');
    background-repeat: no-repeat;
    background-position: top;
}
.login-modal .modal-dialog {
    margin-top: 319px;
    border-radius: 0px;
    -webkit-animation: none;
            animation: none;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    box-shadow: 0 0 10px 10px rgba(1, 162, 245, 0.03);
    opacity: 1;
}
.login-title {
    margin-top: -65px;
    color: rgb(255, 255, 255);
    font-size: 22px;
    font-weight: normal;
    display: block;
    position: absolute;
    margin-left: -20px;
}
.login-username-title {
    color: #999999;
    font-size: 12px;
    margin-left: 14px;
    margin-bottom: 0px;
    margin-top: 0px;
}
.login-password-title {
    color: #999999;
    font-size: 12px;
    margin-left: 14px;
    margin-top: 0px;
}
input::-webkit-input-placeholder {
    　　color: #ddd;
}
.login-icon {
    width: 21px;
    height: 21px;
    background-image: url(../../../static/img/login-icon.png);
    display: inline-block;
    position: absolute;
    margin-top: 15px;
    margin-left: 8px;
}
.login-username-icon {
    background-position: 0px 0px;
}
.login-password-icon {
    background-position: -25px 0px;
}
.btn.reg-btn, .btn.login-forget {
    margin-top: 10px;
}



