
.user-ctrl-table tbody tr:nth-child(odd) {
    background-color: #eee
}
.UserShadow {
    position: absolute;
    top: 2px;
    left: -5px;
    z-index: -10;
}
#Userthead th {
    padding: 5px;
    width: 220px;
    text-align: left;
    height: 40px;
    line-height: 40px;
    font-size: 12px;
    color: #91B0C4;
    padding-left: 20px;
}
#Userthead .role, #Userthead .status {
    width: 140px;
}
#Userthead .operation {
    width: 280px;
}
#Usertbody tr {
    height: 60px;
    line-height: 20px;
}
#Usertbody td {
    text-align: left;
    padding-left: 20px;
    font-size: 12px;
    color: rgb(102, 102, 102);
}
#Usertbody .role {
    color: rgb(254, 80, 77);
}



