
#Userthead .role, #Userthead .status {
    width: 140px;
}
#Userthead .operation {
    width: 280px;
}
#Usertbody .status div {
    margin-left: 10px;
}
#userIcon div {
    width: 38px;
    height: 20px;
    margin-right: 21px;
    border-radius: 16px;
    box-shadow: none;
    display: inline-block;
    text-align: center;
    padding-top: 5px;
    transition: all 1s;
}
.pass span {
    width: 17px;
    height: 17px;
    display: inline-block;
    background: url(../../../static/img/pass.png) no-repeat;
}
.pass:hover {
    background-color: #e3f2fa;
}
.pass:hover span {
    background: url(../../../static/img/Cpass.png) no-repeat;
}
